.App-header {
header {font-family: Arial, Helvetica, sans-serif;}
}

.navbar {
  width: 100%;
  background-color: #121B23;
  overflow: auto;
}

.navbar a {
  float: right;
  padding: 12px;
  color: white;
  text-decoration: none;
  font-size: 17px;
}

.navbar a:hover {
  background-color: #2E50A7;
}

@media screen and (max-width: 500px) {
  .navbar a {
    float: none;
    display: block;
  }
}
